
<div>
  <div v-if="type === 'basic'">
    <ModalBasic
      :name="modalName"
      :title="title"
      :subTitle="subTitle"
      :text="text"
      :width="width"
      :minWidth="minWidth"
      :hideActionButtons="hideActionButtons"
      :confirmButtonText="confirmButtonText"
      :clickToClose="clickToClose"
      v-on="$listeners"
    >
      <slot></slot>
    </ModalBasic>
  </div>

  <div v-if="type === 'subscribe'">
    <ModalSubscribe
      :name="modalName"
      :title="title"
      :subTitle="subTitle"
      :text="text"
      :subscribeText="subscribeText"
      :subscribeMessage="subscribeMessage"
      :notes="notes"
      :width="width"
      :minWidth="minWidth"
      :scrollable="scrollable"
      :proBadge="proBadge"
      :customPreview="customPreview"
      :buttonTitle="buttonTitle"
      :buttonPrice="buttonPrice"
      :buttonVariation="buttonVariation"
      :cancelTitle="cancelTitle"
      v-on="$listeners"
    >
      <template #preview>
        <slot name="preview"></slot>
      </template>
      <slot></slot>
    </ModalSubscribe>
  </div>

  <div v-if="type === 'redirect'">
    <ModalRedirect
      :name="modalName"
      :title="title"
      :text="text"
      :width="width"
      :minWidth="minWidth"
      v-on="$listeners"
    ></ModalRedirect>
  </div>

  <div v-if="type === 'confirmation'">
    <ModalConfirmation
      :name="modalName"
      :subTitle="subTitle"
      :text="text"
      :width="width"
      :minWidth="minWidth"
      :confirmButtonText="confirmButtonText"
      :buttonVariation="buttonVariation"
      v-on="$listeners"
    ></ModalConfirmation>
  </div>

  <div v-if="type === 'welcome-prime'">
    <ModalPrime
      :name="modalName"
      :width="width"
      :minWidth="minWidth"
      :primeButtonText="primeButtonText"
      :hasPrimeCloseButton="hasPrimeCloseButton"
      v-on="$listeners"
    >
      <slot></slot>
    </ModalPrime>
  </div>

  <div v-if="type === 'prime-intro'">
    <ModalPrimeIntro
      :name="modalName"
      :width="680"
      :minWidth="minWidth"
      :primeButtonText="primeButtonText"
      :hasPrimeCloseButton="hasPrimeCloseButton"
      v-on="$listeners"
    >
      <slot></slot>
    </ModalPrimeIntro>
  </div>
</div>
