
<modal
  :name="name"
  :classes="'s-modal-wrapper'"
  :maxWidth="width"
  :minWidth="minWidth"
  height="auto"
  :adaptive="true"
  v-on="$listeners"
>
  <div class="s-modal-container">
    <div class="s-redirect">
      <div class="s-spinner">
        <Spinner :variation="'bars'" />
      </div>
      <h1 class="s-modal-title">{{ title }}</h1>
      <p class="s-modal-text">{{ text }}</p>
    </div>
  </div>
</modal>
