
<modal
  :name="name"
  classes="s-modal-wrapper"
  :width="width"
  :minWidth="minWidth"
  height="auto"
  :adaptive="true"
  v-on="$listeners"
>
  <div class="modal-prime__close" v-if="hasPrimeCloseButton">
    <i class="icon-close" @click="$modal.hide(name)"></i>
  </div>
  <welcome-prime :primeButtonText="primeButtonText" v-on="$listeners">
    <slot></slot>
  </welcome-prime>
</modal>
